import React from 'react';
import {
    BaseProps,
    BordererPresets,
    BreakpointMedium,
    Fonter,
    FontStyle, Marginer, Paddinger, PaddingerPresets,
    Surface,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {HeaderSecondary} from "../Headers";
import {TextPrimary_Center} from "../Text";
import {ColorSet} from "../../styles";

export function TestimonialText({text}: { text: string }) {
    const finalText = `"${text}"`;
    return <TextPrimary_Center
        text={finalText}
        style={{
            ...Paddinger({top: 5, left: 5, right: 5, bottom: 0}),
            ...Fonter({fontStyle: FontStyle.Italic})
        }}/>
}

export interface TestimonialNameProps {
    name: string;
    title: string;
}

export function TestimonialName({name, title}: TestimonialNameProps) {
    return <Surface style={PaddingerPresets._2}>
        <HeaderSecondary text={name}/>
        {title ? <TextPrimary_Center text={title}/> : <></>}
    </Surface>;
}

interface TestimonialProps extends BaseProps {
    text: string;
    name: string;
    title: string;
}

export function Testimonial({style = {}, classes = [], ...props}: TestimonialProps) {
    const {text, name, title} = props;
    const width = useBreakpoint() <= BreakpointMedium ? '90%' : '65%';
    return <Surface style={{...style, overflow: 'auto', width: '100%'}}>
        <Surface style={{...Marginer({top: 4, bottom: 4, right: 'auto', left: 'auto'}), ...BordererPresets._8, width, background: ColorSet.Primary3}}>
            <TestimonialText text={text}/>
            <TestimonialName name={name} title={title}/>
        </Surface>
    </Surface>;
}
