import React from 'react';
import {
    Flexer,
    FlexerPresets,
    FlexWrap,
    Fonter,
    MarginerPresets,
    shiftBreakpoint,
    Surface,
    TextAlign, useBreakpoint
} from '@spinglobal/react-foundations';
import {EmployeeCard, HeaderPrimary, TextPrimary} from '../../Components';
import EMPLOYEES from './employees';

const paragraph1 = 'Our team of experts was selected not just for their impressive resumes, but because they are equally driven and inspired to make the world safer. We are SPIN Global!';

export function Team() {
    const bp = useBreakpoint();
    return <Surface style={MarginerPresets.auto_4}>
        <Surface style={FlexerPresets.ColumnCenter}>
            <HeaderPrimary text='Our Thought Leaders' style={Fonter({textAlign: TextAlign.Center})}/>
            <TextPrimary
                text={paragraph1}
                style={{
                    ...MarginerPresets._4,
                    maxWidth: shiftBreakpoint(bp, -2),
                    ...Fonter({textAlign: TextAlign.Center})
                }}/>
        </Surface>
        <Surface style={{...FlexerPresets.MainCenter, ...Flexer({wrap: FlexWrap.Wrap}), maxWidth: shiftBreakpoint(bp, -1), ...MarginerPresets.auto}}>
            {EMPLOYEES.sort(function sort(a, b) {
                if ( a.last_name < b.last_name ){
                    return -1;
                }
                if ( a.last_name > b.last_name ){
                    return 1;
                }
                return 0;

            }).map(e => <EmployeeCard {...e}/>)}
        </Surface>
    </Surface>;
}
