import React from 'react';
import {BaseHeaderProps, Header, HeaderTag, useBreakpoint} from '@spinglobal/react-foundations';
import {
    style__font__HeaderTertiary
} from "../../../styles";

export function HeaderTertiary({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    const bp = useBreakpoint();
    return <Header
        level={HeaderTag.Heading3}
        style={{...style__font__HeaderTertiary(bp), ...style}}
        classes={classes}
        {...headerProps}
    />
}
