import {CSSProperties} from 'react';
import {ColorSet} from "./color-constants";
import {FontSet} from "./font-constants";
import {Fonter, FontWeight, TextAlign} from "@spinglobal/react-foundations";

// Text Primary
export const style__font__TextPrimary = (bp: number): CSSProperties => {
    return {
        ...Fonter({
            fontFamily: FontSet.Voice,
            level: 3,
            color: ColorSet.Neutral2,
            breakpoint: bp
        }),
        userSelect: 'none'
    }
}

export const style__font__TextPrimary_White = (bp: number): CSSProperties => {
    return {
        ...style__font__TextPrimary(bp),
        color: ColorSet.Primary3
    }
}

export const style__font__TextPrimary_Center = (bp: number): CSSProperties => {
    return {
        ...style__font__TextPrimary(bp),
        textAlign: TextAlign.Center
    }
}

// Header Primary
export const style__font__HeaderPrimary = (bp: number): CSSProperties => {
    return {
        ...Fonter({
            fontWeight: FontWeight.W7,
            level: 7,
            fontFamily: FontSet.Information,
            breakpoint: bp,
            letterSpacing: 2,
            textAlign: TextAlign.Center,
            color: ColorSet.Primary1
        }),
        userSelect: 'none'
    }
}

export const style__font__HeaderPrimary_Upper = (bp: number): CSSProperties => {
    return {
        ...style__font__HeaderPrimary(bp),
        textTransform: 'uppercase'
    }
}

export const style__font__HeaderPrimary_White = (bp: number): CSSProperties => {
    return {
        ...style__font__HeaderPrimary(bp),
        color: ColorSet.Primary3
    }
}

// Header Secondary
export const style__font__HeaderSecondary = (bp: number): CSSProperties => {
    return {
        ...Fonter({
            level: 4,
            color: ColorSet.Primary1,
            fontFamily: FontSet.Information,
            breakpoint: bp,
            letterSpacing: 2,
            textAlign: TextAlign.Center
        }),
        userSelect: 'none'
    }
}

// Header Tertiary
export const style__font__HeaderTertiary = (bp: number): CSSProperties => {
    return {
        ...style__font__HeaderPrimary_White(bp),
        ...Fonter({breakpoint: bp, level: 3})
    }
}
