import React, {CSSProperties, PropsWithChildren} from 'react';
import {HeaderPrimary, HeaderSecondary, TextPrimary_Center} from '../../Components';
import CONTENT from './about';
import {
    BordererPresets,
    BreakpointLarge,
    FlexDirection,
    Flexer,
    FlexerPresets,
    FlexMainAxis,
    Image,
    Shadower,
    Surface,
    useBreakpoint, Marginer, PaddingerPresets, MarginerPresets
} from '@spinglobal/react-foundations';

function AboutParagraph({text}: { text: string }) {
    return <TextPrimary_Center text={text} style={{
        padding: '0 18px',
        display: 'inline',
    }}/>
}

function AboutImage() {
    const width = useBreakpoint() <= BreakpointLarge ? '80%' : '45vw';
    return <Surface style={{...MarginerPresets.auto, alignSelf: 'start', width}}>
        <Image
            src='https://media.istockphoto.com/photos/team-teamwork-business-join-hand-together-concept-power-of-volunteer-picture-id1017183652?s=2048x2048'
            alt='Recovering From Disaster'
            style={{
                maxWidth: 800,
                width,
                display: 'block',
                userSelect: 'none',
                ...MarginerPresets.auto_0,
                ...BordererPresets._8,
                ...Shadower({
                    voffset: 4,
                    spread: 4,
                    blur: 8,
                    color: 'rgba(0, 0, 0, .25)'
                })
            }}/>
    </Surface>
}

function AboutRow({children, style}: PropsWithChildren<{ style?: CSSProperties }>) {
    const flexer = useBreakpoint() <= BreakpointLarge ? FlexerPresets.ColumnCenter : FlexerPresets.MainCenter;
    return <Surface style={{...style, ...PaddingerPresets._0, ...flexer}}>
        {children}
    </Surface>;
}

function AboutSection(obj: { leadin: string, text: string }) {
    const {leadin, text} = obj;
    return <Surface style={{textAlign: 'center', ...MarginerPresets._3}}>
        <HeaderSecondary text={leadin} style={{marginRight: -14, display: 'inline'}}/>
        <AboutParagraph text={text}/>
    </Surface>
}

export function AboutUs() {
    return <Surface style={{...FlexerPresets.Center, width: '100%'}}>
        <HeaderPrimary text='Our Mission' style={Marginer({top: 4, bottom: 4})}/>
        <AboutRow style={{marginRight: 18}}>
            <Surface style={Flexer({direction: FlexDirection.Column, main: FlexMainAxis.SpaceAround})}>
                {CONTENT[0].map(e => <AboutSection key={e.leadin} {...e}/>)}
            </Surface>
            <AboutImage/>
        </AboutRow>
        <AboutRow>
            {CONTENT[1].map(e => <AboutSection key={e.leadin} {...e}/>)}
        </AboutRow>
    </Surface>;
}
