import React from 'react';
import {BaseHeaderProps, Header, HeaderTag, useBreakpoint} from '@spinglobal/react-foundations';
import {
    style__font__HeaderSecondary
} from "../../../styles";

export function HeaderSecondary({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    const bp = useBreakpoint();
    return <Header level={HeaderTag.Heading2} style={{...style__font__HeaderSecondary(bp), ...style}} classes={classes} {...headerProps}/>
}
