import React, {useEffect, useRef} from 'react';
import {Testimonial} from "../../Components/Testimonial";
import {ParallaxScreen} from "../ParallaxScreen";
import {ServicesExpertise} from "../ServicesExpertise/ServicesExpertise";
import {Team} from "../Team/Team";
import {Footer} from "../Footer";
import testimonials from './testimonials';
import {ArcGis} from "../../Components";
import {ColorSet} from "../../styles";
import {useStyle, idManager} from "@spinglobal/react-foundations";

function FilledTestimonial({text, name, title}: { text: string, name: string, title: string }) {
    return <Testimonial
        text={text}
        name={name}
        title={title}
        style={{zIndex: 1, position: 'relative', background: ColorSet.Primary2}}
    />
}

export function App() {
    const ref = useRef<string>(idManager.genId(2));
    if (ref.current) idManager.addId(ref.current);
    const selector = `.${ref.current}`;
    useStyle(selector, {visibility: 'initial !important'} as any);

    useEffect(() => {
        const {current} = ref;
        return () => {
            idManager.removeId(current);
        }
    }, []);

    return <div style={{visibility: 'hidden', overflow: 'hidden', position: 'relative'}} className={ref.current}>
        <ParallaxScreen/>
        <FilledTestimonial {...testimonials[0]}/>
        <ServicesExpertise/>
        <FilledTestimonial {...testimonials[1]}/>
        <Team/>
        <FilledTestimonial {...testimonials[2]}/>
        <ArcGis/>
        <FilledTestimonial {...testimonials[3]}/>
        <Footer/>
    </div>;
}
