import React from 'react';
import {BaseTextProps, Paragraph, useBreakpoint} from "@spinglobal/react-foundations";
import {
    style__font__TextPrimary,
    style__font__TextPrimary_Center,
    style__font__TextPrimary_White
} from "../../../styles";

export function TextPrimary({style = {}, classes = [], ...textProps}: BaseTextProps) {
    const bp = useBreakpoint();
    return <Paragraph classes={classes} style={{...style__font__TextPrimary(bp), ...style}} {...textProps}/>
}

export function TextPrimary_White({style = {}, classes = [], ...textProps}: BaseTextProps) {
    const bp = useBreakpoint();
    return <Paragraph classes={classes} style={{...style__font__TextPrimary_White(bp), ...style}} {...textProps}/>
}

export function TextPrimary_Center({style = {}, classes = [], ...textProps}: BaseTextProps) {
    const bp = useBreakpoint();
    return <Paragraph classes={classes} style={{...style__font__TextPrimary_Center(bp), ...style}} {...textProps}/>
}
